<template>
  <v-parallax dark :height="parallaxHeight" src="@/assets/iconsa/ic_backgroundlogin.png">
    <v-row align="center" justify="center">
      <v-col class="text-center mx-auto" cols="12">
        <h1 class="display-1 font-weight-bold mb-4 mx-auto">Encuentra los productos que necesitas..</h1>
        <!---->
        <v-container>
          <v-row>
            <v-col
              class="mb-xs-1"
              cols="12"
              lg="5"
              md="5"
              sm="12"
              xs="12"
              offset-md="3"
              offset-lg="3"
            >
              <v-autocomplete
                :height="50"
                v-model="model"
                :loading="isLoading"
                :search-input.sync="search"
                @keyup.enter="submit($event, search)"
                flat
                :cache-items="false"
                clearable
                hide-details
                hide-selected
                hide-no-data
                label="Busca cualquier producto..."
                append-icon
                prepend-inner-icon="mdi-magnify"
                solo
              >

                <!-- :items="dataResultsSearchByStore"
                item-text="name"
                item-value="id" -->
                <template v-slot:item="{ item }">
                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <v-icon>mdi-chevron-right</v-icon>
                  </v-list-item-icon>
                </template>
              </v-autocomplete>
            </v-col>
            <v-col cols="12" lg="1" md="1" sm="12" xs="12">
              <v-btn :height="50" large color="white" @click="submit($event, search)">Buscar</v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-col>
    </v-row>
  </v-parallax>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "Search",
  computed: {
    ...mapState("products", ["dataResultsSearchByStore"])
  },
  data() {
    return {
      parallaxHeight: 500,
      isLoading: false,
      model: null,
      search: null
    };
  },
  methods: {
    ...mapActions("products", ["getProductsByStoreSearch"]),
    submit(event) {
      event.target.blur();
      this.$router.push({ name: "results", query: {input_search: this.search} }).catch(err => err);
    }
  },
  watch: {
    search(val) {
    }
  }
};
</script>
