<template>
  <v-container cols="10">
    <v-col cols="12">
      <v-card class="pa-3" elevation="10">
        <v-container fluid>
          <v-row>
            <v-col cols="12">
              <p class="text-section-header">Conoce nuestras promociones</p>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              class="mx-auto"
              v-for="(item, index) in dataStoresByStore"
              :key="index"
              cols="12"
            >
              <v-sheet class="mx-auto" elevation="10" max-width="1400">
                <v-row class="ma-auto">
                  <span class="text-section-store">{{ item.name}}</span>
                  <v-btn
                    class="btn-ver-mas"
                    outlined
                    small
                    color="black"
                    @click="goToStores(item.id)"
                  >
                    Ver más
                    <v-icon right color="primary">mdi-chevron-right</v-icon>
                  </v-btn>
                </v-row>
                <!-- <v-chip-group v-if="showCategories" class="px-2" :column="true">
                  <v-chip
                  label
                  style="text-transform: lowercase"
                    color="primary"
                    text-color="white"
                    @click="goToStores(item.id)"
                    v-for="tag in item.categories"
                    :key="tag.id"
                  >{{ tag.name }}</v-chip>
                </v-chip-group> -->
                 <store-categories :categories="item.categories" :store_id="item.id"></store-categories>
                <v-slide-group v-if="showPromotions" :show-arrows="false">
                  <v-slide-item
                    v-for=" (mission, index) in getMissions(item.id)"
                    :key="index"
                    v-slot:default="{ active, toggle }"
                  >
                    <v-card
                      :color="active ? 'primary' : 'grey lighten-1'"
                      class="ma-3"
                      height="220"
                      width="280"
                      @click="toggle"
                    >
                      <v-img
                        :src="mission.image"
                        class="white--text align-end"
                        gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                        height="220px"
                      >
                        <v-card-title v-text="mission.title"></v-card-title>
                      </v-img>
                    </v-card>
                  </v-slide-item>
                </v-slide-group>
              </v-sheet>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-col>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
import Categories from "@/components/Home/Categories";
export default {
  name: "Promotions",
  components: {
    StoreCategories: Categories,
  },
  computed: {
    ...mapState("products", ["dataMissionsByStore", "dataStoresByStore"])
  },
  data() {
    return {
      subcategories: [],
      showSubcategories: false
    };
  },
  methods: {
    getMissions(storeId) {
      if (this.dataMissionsByStore.length > 0) {
        this.dataMissionsByStore.forEach(item => {
          if (item.id == storeId) {
            return item.missions;
          }
        });
      }
      return [];
    },
    goToStores(id) {
      this.$router.push({ name: "store", params: { id } });
    },
    filterSubcategories(data) {
      if (data) {
        this.showSubcategories = true;
        this.subcategories = data;
      } else {
        this.showSubcategories = false;
        this.subcategories = [];
      }
    }
  },
  props: {
    showPromotions: Boolean,
    showCategories: Boolean
  }
};
</script>
<style>
.text-section-store {
  color: black;
  text-align: left;
  font-size: 25px;
  font-weight: 700;
  text-transform: initial;
  padding-left: 15px;
}

.btn-ver-mas {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  text-transform: inherit;
}
